roductos y retroalimentándose de su impacto en el ambiente. Este proceso requiere una coordinación efectiva, subrayando la relevancia del liderazgo gerencial.")]),_c('div',{attrs:{"titulo":"Clasificación por constitución y jerarquía de sistemas"}},[_vm._v("Adicionalmente, los sistemas pueden ser abstractos, como planes e ideas, o físicos, como maquinaria. Cada sistema está integrado en jerarquías mayores: macrosistemas, que los contienen, y suprasistemas, que agrupan múltiples sistemas interrelacionados. En las empresas, los elementos claves incluyen insumos, procesos, productos y retroalimentación, interactuando dinámicamente para garantizar eficacia organizacional.")]),_c('div',{attrs:{"titulo":"Interdisciplinariedad"}},[_vm._v("Es un eje central en esta teoría, al permitir que modelos de distintas ciencias, como la sociología, enriquezcan el análisis organizacional. Walter Bucley y otros autores señalan que estos enfoques no solo fortalecen la administración científica, sino que también superan visiones unidimensionales, promoviendo una comprensión integral y práctica de las organizaciones.")])])],1)]),_vm._m(21),_vm._m(22),_vm._m(23),_c('div',{staticClass:"bg-full-width bg-color-6 mb-5",attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"px-5 py-4"},[_c('div',{staticClass:"row justify-content-center mb-0"},[_c('div',{staticClass:"col-sm-12 col-lg-10"},[_c('ImagenInfografica',{staticClass:"color-secundario mb-0",scopedSlots:_vm._u([{key:"imagen",fn:function(){return [_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t2/38.svg")}})])]},proxy:true}])},[_c('div',{staticClass:"tarjeta bg-color-white p-3",attrs:{"x":"72%","y":"75%","numero":"+"}},[_c('p',{staticClass:"fw-bold mb-3"},[_vm._v("Resistencia")]),_c('p',{staticClass:"mb-0"},[_vm._v("Establece medidas para la protección y Por su naturaleza abierta, las organizaciones son vulnerables a influencias externas. Sin embargo, deben desarrollar mecanismos para superar estas afectaciones, manteniendo su e