idad de sus interacciones.")])]),_c('div',{staticClass:"tarjeta bg-color-white p-3",attrs:{"x":"23.9%","y":"69.5%","numero":"+"}},[_c('p',{staticClass:"fw-bold mb-3"},[_vm._v("Homeostasis")]),_c('p',{staticClass:"mb-0"},[_vm._v("El equilibrio es fundamental para las organizaciones, permitiéndoles avanzar hacia sus objetivos, mientras mantienen estabilidad y se adaptan a los cambios necesarios para sobrevivir en un entorno cambiante.")])])])],1)])])]),_vm._m(24),_vm._m(25),_c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"flip-up"}},[_vm._m(26),_c('div',{staticClass:"col-sm-12 col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{attrs:{"titulo":"Tamaño de la organización"}},[_vm._v("Gestionar una empresa con pocos empleados no implica el mismo esfuerzo administrativo que dirigir una con 10,000 trabajadores. En el segundo caso, es necesario implementar una estructura más elaborada y contar con habilidades gerenciales avanzadas.")]),_c('div',{attrs:{"titulo":"Tecnologías rutinarias"}},[_vm._v("El uso de tecnologías está directamente relacionado con la estructura y el tamaño de la empresa. Aquellas con necesidades tecnológicas más avanzadas requieren ajustes específicos y una adecuada integración operativa, a diferencia de las organizaciones con demandas tecnológicas menos complejas o no rutinarias.")]),_c('div',{attrs:{"titulo":"Incertidumbre del entorno"}},[_vm._v("Los constantes cambios sociales, científicos y económicos propios de los tiempos actuales, generan un entorno organizacional lleno de incertidumbre. Las estrategias desarrolladas en contextos volátiles deben responder con rapidez en el aprendizaje, adaptabilidad estructural y un equilibrio interno eficaz, lo que las diferencia de las que se aplican en ambientes más estables.")]),_c('div',{attrs:{"titulo":"Diferencias individuales"}},[_vm._v("Las personas presentan diversidad en sus motivaciones, objetivos y expectativas. Aunque pueden compartir ciertos puntos en común, cada in